<template>
    <yd-layout>
        <div class="mainContentFluid">
            <div class="approval">
                选择审批人
            </div>
            <div class="fluidWidth">
                <div class="tripAppro">
                    <div class="name_circle">
                        <ul>
                            <li v-for="(item, index) in approver">
                                <div class="lastname" v-bind:class="{'check':checkNum == index}" v-on:click="checkNum==index ? checkNum=99:checkNum=index">
                                    {{item.empname.substring(0,1)}}
                                    <div class="nameClick">
                                    </div>
                                    <div class="imgClick">
                                    </div>
                                </div>
                                <div class="name" v-text="item.empname">
                                </div>
                            </li>
                        </ul>
                        <div class="clear"></div>
                    </div>
                </div>
            </div>
        </div>
        <yd-tabbar slot="tabbar" v-if="leaderFlag=='true'">
            <yd-button v-on:click.native="sencondSend()" size="large" type="primary" bgcolor="#00B6FC" color="#fefefe">发送</yd-button>
        </yd-tabbar>
        <yd-tabbar slot="tabbar" v-if="leaderFlag=='false'">
            <yd-button v-on:click.native="againSend()" size="large" type="primary" bgcolor="#00B6FC" color="#fefefe">发送</yd-button>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'selectLeader',
    data() {
        return {
            queryTypeArr: [23, 24, 25],
            checkNum: 99,
            approver: [],
            selectLeader: this.$route.name,
            leaderFlag: this.$route.params.leaderFlag
        }
    },
    mounted() {
        document.setTitle('请休假');
        let vm = this;
        if (vm.leaderFlag == 'false') {
            vm.approver = vm.confirmHoliday.approver;
        } else {
            vm.approver = vm.holidayInfo.approver;
        }
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo',
            confirmHoliday: 'getConfirmHoliday',
            holidayInfo: 'getHolidayInfo'
        })
    },
    methods: {
        toBack(type) {
            this.$emit('botSetflag', type);
        },
        sencondSend() {
            let vm = this;
            if (vm.checkNum == 99) {
                vm.$dialog.toast({
                    mes: '请选择审批人',
                    timeout: 2000
                });
            } else {
                if (vm.checkNum != 99) {
                    vm.leaderIDs = vm.approver[vm.checkNum].operatorid;
                }
                vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/doApprove', {
                    'type': 'askforleave',
                    'approveType': vm.holidayInfo.approveType,
                    'workItemID': vm.$route.params.workId,
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': '',
                    'opinion': '',
                    'workTitle': vm.userInfo.deptname + vm.holidayInfo.appLyName + '请假申请',
                    'ext': '{}'
                }, function (res) {
                    vm.$dialog.toast({
                        mes: '提交申请成功',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle/');
                        }
                    });
                });
            }
        },
        againSend() {
            let vm = this;
            if (vm.checkNum == 99) {
                vm.$dialog.toast({
                    mes: '请选择审批人',
                    timeout: 2000
                });
            } else {
                if (vm.checkNum != 99) {
                    vm.leaderIDs = vm.approver[vm.checkNum].operatorid;
                }
                vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/doApprove', {
                    'type': 'askforleave',
                    'approveType': vm.confirmHoliday.approveType,
                    'workItemID': vm.$route.params.workId,
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': '',
                    'opinion': '',
                    'workTitle': vm.userInfo.deptname + vm.confirmHoliday.appLyName + '请假申请',
                    'ext': '{"realStartDate":"' + vm.confirmHoliday.realStartDate + '","realEndDate":"' + vm.confirmHoliday.realEndDate + '","realDays":"' + vm.confirmHoliday.realDays + '"}'
                }, function (res) {
                    vm.$dialog.toast({
                        mes: '提交申请成功',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle/');
                        }
                    });
                });
            }
        }
    }
}
</script>
<style scoped>
.check .nameClick {
    width: 0.81rem;
    height: 0.81rem;
    margin: 0 auto;
    border-radius: 1rem;
    text-align: center;
    z-index: 2;
    opacity: 0.5;
    background-color: #256f7b;
    position: absolute;
    top: 0;
    right: 0;
}

.check .imgClick {
    width: 0.81rem;
    height: 0.81rem;
    margin: 0 auto;
    border-radius: 1rem;
    text-align: center;
    z-index: 99;
    position: absolute;
    top: 11px;
    right: -6px;
    background-image: url(../../../static/images/dh.png);
    background-repeat: no-repeat;
    background-size: 0.6rem;
}

.name_circle {
    margin-top: 0.5rem;
}

.approval {
    font-size: 0.32rem;
    padding-left: 0.53rem;
    padding-top: 0.27rem;
    font-weight: normal;
    border-bottom: 1px dashed #eaeaea;
}

.mainContentFluid {
    margin-top: 0;
    height: 100%;
}

.last_send {
    height: 100%;
}

.assistBottom {
    border-top: none;
}

.examineLogo {
    height: 0.99rem;
    margin-top: 0;
    border-right: none;
}

.examineLogo .yd-flexbox {
    line-height: 0.99rem;
}

.examineLogo_txt {
    color: #fff;
    font-size: 0.36rem;
    font-weight: normal;
}

.btnOne {
    background: #fafafa;
    letter-spacing: 0.1rem;
    border: 1px solid #eaeaea;
}

.btnTwo {
    background-color: #46a4f0;
}

.yd-tabbar {
    padding: 0;
}

.yd-tabbar:after {
    border-top: none;
}

.toBack {
    color: #333;
}

.yd-btn-block {
    height: .99rem;
    margin-top: 0;
    border-radius: 0;
}
</style>


